import React from 'react';
import ReactDom from 'react-dom';
import { MuiThemeProvider } from '@material-ui/core';
import App from './App';
import defaultTheme from './theme';

const StyledApp = () => (
  <MuiThemeProvider theme={defaultTheme}>
    <App />
  </MuiThemeProvider>
);

ReactDom.render(
  <StyledApp />,
  document.getElementById('root'),
);
