import React, { useState } from 'react';
import { BrowserRouter as Router } from 'react-router-dom';
import { makeStyles, createStyles } from '@material-ui/core';
import SideNavigationBar from './components/SideNavbar/SideNavbar';
import { appNavigationItems } from './models/navigationMenuItems';
import AppRouter from './AppRouter';
import PageHeader from './components/shared/PageHeader';

const useStyles = makeStyles((theme) => createStyles({
  root: {
    display: 'flex',
  },
  appContainer: {
    flexGrow: 1,
    paddingLeft: theme.spacing(1),
  },
  mainContent: {
    paddingTop: theme.spacing(10),
  },
}));

const App:React.FunctionComponent = () => {
  const classes = useStyles();
  const [header, setHeader] = useState('');

  return (
    <Router>
      <div className={classes.root}>
        <SideNavigationBar
          navigationBarItems={appNavigationItems}
          onNavItemSlectionChanged={(pageTitle:string) => setHeader(pageTitle)}
        />
        <div className={classes.appContainer}>
          <PageHeader pageTitle={header} />

          <div className={classes.mainContent}>
            <AppRouter />
          </div>
        </div>
      </div>
    </Router>
  );
};

export default App;
