import React, { useState, useCallback } from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core';
import {
    CssBaseline, Drawer, List, Icon,
} from '@material-ui/core';
import { NavigationBarItem } from './SideNavbarItem';
import ListItemLink from '../shared/ListItemLink';
import { drawerWidth } from '../shared/constants';

const useStyles = makeStyles((theme:Theme) => createStyles({
    root: {
        display: 'flex',
    },
    drawer: {
        width: drawerWidth,
        flexShrink: 0
    },
    drawerPaper: {
        width: drawerWidth,
        backgroundColor: theme.palette.primary.dark
    },
    navbarItemRoot: {
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        width: "inherit"
    },
    icon: {
        color: theme.palette.common.white
    },
    selectedIcon: {
        color: theme.palette.secondary.light
    }
}));

interface Props {
    navigationBarItems: NavigationBarItem[],
    onNavItemSlectionChanged: (header:string) => void,
}

const SideNavigationBar : React.FC<Props> = (props:Props) => {
    const { navigationBarItems, onNavItemSlectionChanged } = props;
    const classes = useStyles();
    const [selectedNavItem, setSelectedNavItem] = useState(navigationBarItems[0]);

    const onLinkItemClicked = useCallback((value:NavigationBarItem) => {
        setSelectedNavItem(value);

        if (onNavItemSlectionChanged) {
            onNavItemSlectionChanged(value.tooltip);
        }
    },[setSelectedNavItem, onNavItemSlectionChanged]);

    return (
        <div>
            <CssBaseline />
            <Drawer
                anchor="left"
                className={classes.drawer}
                classes={{
                    paper: classes.drawerPaper
                }}
                variant="permanent"
            >
                <List>
                    {
                        navigationBarItems &&
                        navigationBarItems.map((value:NavigationBarItem) => (
                            <ListItemLink
                                href={value.link}
                                key={value.id}
                                title={value.tooltip}
                                data-testid={`item-${value.id}`}
                                onNavLinkClicked={() => onLinkItemClicked(value)}
                            >
                                {
                                    (value.icon || value.fontAwesomeClassName) &&
                                    <div className={classes.navbarItemRoot}>
                                        {
                                            value.fontAwesomeClassName
                                            ? <Icon 
                                                data-testid={`item-fontawesome-icon-${value.id}`}
                                                className={`
                                                    ${value.fontAwesomeClassName} 
                                                    ${classes.icon} 
                                                    ${value.id === selectedNavItem.id && classes.selectedIcon}
                                                `} />
                                            : <Icon 
                                                data-testid={`item-icon-${value.id}`}>
                                                    {value.icon}
                                                </Icon>
                                        }
                                    </div>
                                }

                                {
                                    value.label && 
                                    <span 
                                        data-testid={`item-label-${value.id}`}>
                                            {value.label}
                                    </span>
                                }
                            </ListItemLink>
                        ))
                    }
                </List>
            </Drawer>
        </div>
    );
}

export default SideNavigationBar;